.main_left{
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: var(--blue);
}

h1,h3{
    font-weight: 300 !important;

}

.main_left h1{
    font-size: 4.5rem;
}
.main_right{
display: flex;
flex-direction: column;
justify-content: center;

}

@media(max-width:480px){
    .main_left h1{
        font-size: 2.1rem;
        text-align: center;
    }
    .main_left h3{
        text-align: center;
    }

}