.cont1{
    margin: 50px auto;
}
.cont1 h2{
    color: var(--blue);
    padding-bottom: 1rem;
    border-bottom: 5px solid var(--orange);
    margin-bottom: 1rem;
}



.card{
    margin: 10px auto;
    padding: 0px;
    box-shadow: 0px 5px 5px rgba(0,0,0,0.5);
    transition: 0.5s ease-in-out;
    width: 20rem;
}

.card-img-top{
    height: 250px;
}

.card:hover{
    box-shadow: none;
    transform: scale(1.1);
    transition: 0.5s ease-in-out;
}

a{
    color: transparent;
    text-decoration: none !important;
}

.card-title{
    color: var(--blue) !important;
    text-align: center;
}

.main-list{
   width: 60%;
   margin: 0px auto;
}

.yt{
    width:560px ;
    height:315px;

}

.code{
    width:75%;
    margin:0px auto;
}




@media(max-width:980px){
    .main-list{
        width: 80%;
    }
    .yt{
        width: 500px;
    }
    .code{
        width: 85%;
    }
}

@media(max-width:450px){
    .card{
        width: 18rem;
    }
    .main-list{
        width: 100%;
    }
    .yt{
        width: 300px;
        height: 220px;
    }
    .code{
        width: 95%;
    }
}